import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';
import { SaalryReleaseServiceService } from 'src/app/core/services/saalry-release-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { ToWords } from 'to-words';


@Component({
  selector: 'app-saalry-release-view',
  templateUrl: './saalry-release-view.component.html',
  styleUrls: ['./saalry-release-view.component.scss']
})
export class SaalryReleaseViewComponent implements OnInit {
  public employeeId:any=[];
  public salaryData:any=[];
  public finalAmount: any;
  public totaltotalNetPayable:any = 0
  public totalDeduct:any = 0
  public totalEarning:any = 0
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));

  
  toWords = new ToWords({
    localeCode: 'en-IN',
    converterOptions: {
      currency: true,
      ignoreDecimal: false,
      ignoreZeroCurrency: true,
    }
  });
  calcBasic: number;
  calcHRA: number;
  public addErningFlag:boolean=false
  public adddeductFlag:boolean=false
  public adjustEarnAmount:any=0;
  public adjustDeductAmount:any=0;

  constructor(public dialogRef: MatDialogRef<SaalryReleaseViewComponent>,@Inject(MAT_DIALOG_DATA) public employeeData: any,private saalryReleaseServiceService:SaalryReleaseServiceService,private toastService: ToastNotificationService,
  private titleCasePipe: TitleCasePipe,private roleService:RolesPermissionsService,) { }

  ngOnInit(): void {
    this.employeeId=this.employeeData.employeeId;
    this.GetsalryDataByEmployeeId();
    this.getProfileData();

  }
  GetsalryDataByEmployeeId(){
    this.saalryReleaseServiceService.getAllSalaryDataByEmployeeId({},this.employeeId).then((res:any)=>{
      this.salaryData= res.data.employee
      this.totaltotalNetPayable=res.data.totalNetPayable
      this.finalAmount= res.data.totalNetPayable;
      this.convertToWord(this.finalAmount)
      console.log("res",this.salaryData);

      this.totalDeduct = res.data.deduction
      this.totalEarning= res.data.grossEarning

  

    }
    ,
    (err) => {
      if (err.error.expose) {
        // this.toastService.openErrorSnackBar(
        //   this.titleCasePipe.transform(err.error.error_message)
        // );
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      } else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    }
    )

  }
  getProfileData(){
    this.roleService.getEmployeeById({}, this.employeeId).then((res:any)=>{
      console.log("Payroll employee by id",res)
    },(err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    }

    )

  }

  convertToWord(num) {
    this.finalAmount = this.toWords.convert(num);
  }
  onclear(){
    this.dialogRef.close();
  }
  getAllOffersType(){

  }
  AddEarning(){
    this.addErningFlag=!this.addErningFlag
  }
  AddDeduct(){
    this.adddeductFlag=!this.adddeductFlag
  }
  AdjustAmount(e){
     this.adjustEarnAmount = parseInt((e.target as HTMLInputElement).value);
    console.log('Entered value:', this.adjustEarnAmount);
    let body={
      "grossType": "EARNING",   //gross type should be either EARNING OR DEDUCTION
      "name": "Adjust Amount",
      "total": this.adjustEarnAmount,
      "employeeId":this.employeeId
     
    }
    this.saalryReleaseServiceService.PostEarningDeduct(body,this.currentUser.id).then((res:any)=>{
      // this.toastService.openSnackBar("Earning Details Added Successfully!!!");
      this.toastService.toastMsg({
        title: "Success",
        content: "Earning Details Added Successfully!!!",
      });
      this.saalryReleaseServiceService.notifyPartyAdded();
      this.addErningFlag=false;
      this.dialogRef.close();
    },(err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }
  deductAmount(e){
    this.adjustDeductAmount = parseInt((e.target as HTMLInputElement).value);
    console.log('Entered value:', this.adjustDeductAmount);
    let body={
      "grossType": "DEDUCTION",   //gross type should be either EARNING OR DEDUCTION
      "name": "Adjust Amount",
      "total": this.adjustDeductAmount,
      "employeeId":this.employeeId
    }
    this.saalryReleaseServiceService.PostEarningDeduct(body,this.currentUser.id).then((res:any)=>{
     this.toastService.toastMsg({
          title: "Success",
          content: "Deduction Details Added Successfully!!!",
        });
      // this.toastService.openSnackBar("Deduction  Details Added Successfully!!!");
      this.saalryReleaseServiceService.notifyPartyAdded();
      this.adddeductFlag=false;
      this.dialogRef.close();
    },(err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    })
  }

    isNumeric(value: any): boolean {
    return !isNaN(parseFloat(value)) && isFinite(value);
  }

}
