import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

// Main component
import { PayrollComponent } from './payroll.component';

// Sub-components
import { CreateStructureComponent } from './create-structure/create-structure.component';
import { PayrollListComponent } from './payroll-list/payroll-list.component';
import { SetupPayrollComponent } from './setup-payroll/setup-payroll.component';

// Child components of payload list
import { AssignPayrollComponent } from './assign-payroll/assign-payroll.component';
import { EditPayrollComponent } from './edit-payroll/edit-payroll.component';

// Child component of create structure
import { NewStructureComponent } from './new-structure/new-structure.component';
import { SaalryReleaseListComponent } from './saalry-release-list/saalry-release-list.component';

const routes: Routes = [
  {
    path:'',
    component: PayrollComponent,
    children: [
      {
        path:'',
        redirectTo: 'payroll-list',
        pathMatch: 'full'
      },
      {
        path:'payroll-list',
        component:PayrollListComponent,
      },
      {
        path:'assign-payroll',
        component:AssignPayrollComponent,
      },
      {
        path:'edit-payroll',
        component:EditPayrollComponent,
      },
      {
        path:'setup-payroll',
        component:SetupPayrollComponent,
      },
      {
        path:'create-structure',
        component:CreateStructureComponent,
      },
      {
        path:'new-structure',
        component:NewStructureComponent,
      },
      {
        path:'release-salary',
        component:SaalryReleaseListComponent
      }
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PayrollRoutingModule { }
