import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { SaalryReleaseViewComponent } from '../saalry-release-view/saalry-release-view.component';
import { SaalryReleaseServiceService } from 'src/app/core/services/saalry-release-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { TitleCasePipe } from '@angular/common';
import { CommonService } from 'src/app/core/services/common.service';
import { PayrollService } from 'src/app/core/services/payroll.service';

@Component({
  selector: 'app-saalry-release-list',
  templateUrl: './saalry-release-list.component.html',
  styleUrls: ['./saalry-release-list.component.scss']
})
export class SaalryReleaseListComponent implements OnInit {
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  public payrollCost:any=0;
  public employeeNetPay:any=0;
  public employeeLength=0
  public salaryData=[]
  public checkSelectsinglefalse: boolean = false;
  public submitted: boolean = false;
  public upiCheckedId = [];
  public upiCheckedId1 = [];

  constructor( private dialog: MatDialog,private saalryReleaseServiceService:SaalryReleaseServiceService,private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private commonService: CommonService,  private payrollService: PayrollService,) { }
  ngOnInit(): void {
    this.getSalaryData();
    this.saalryReleaseServiceService.partyAdded$.subscribe(() => {
      // Fetch the updated list of users
      this.getSalaryData();
    });
  }
  getSalaryData(){
    this.saalryReleaseServiceService.getAllSalaryData({},this.currentUser.id).then((res:any)=>{
      this.salaryData= res.data.employee;
      this.payrollCost=res.data.payrollCost;
      this.employeeNetPay=res.data.employeeNetPay;
      this.employeeLength=this.salaryData.length
      console.log("Salary Details",this.salaryData);
    },
    (err) => {
      if (err.error.expose) {
        // this.toastService.openErrorSnackBar(
        //   this.titleCasePipe.transform(err.error.error_message)
        // );
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      } else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    }
    )
  }
  openViewSalaryModel(employeeId:any){
    const dialogRef: MatDialogRef<SaalryReleaseViewComponent> = this.dialog.open(SaalryReleaseViewComponent, {
      width: '50%',
      height : '100vh',
      data: {employeeId:employeeId},
      panelClass: 'custom-dialog-edit-payroll',
      disableClose:true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });

  }
    submitSalary(){
    this.submitted = true;
    if (this.upiCheckedId.length == 0) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Please Select The Employee You Want To submit.",
      });
      return false;
    } else{
      let body = {
        "ids": this.upiCheckedId,
        "details": {
          "name": "test",
          "description": "test description"
      }
      }
      this.payrollService.salarySubmit(body,this.currentUser.id).then((res) => {
        console.log(res);
        this.toastService.toastMsg({
          title: "Success",
          content: "Salary submitted Successfully!!!",
          });
        this.commonService.notifyDataAdded();
        this.submitted = false;
        
      },
        (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
          }
          else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went Wrong.",
            });
          }
  
        })
    }
  }
    // Code for single selction.
   onChekedsingleCheckfalse(e, id) {
    console.log("checked", e.target.checked)

    if (e.target.checked == true) {
      // this.archiveIconfalse = true;
      this.upiCheckedId.push(e.target.value);
      console.log("this.upiCheckedId", this.upiCheckedId)

    }
    else {

        var index = this.upiCheckedId.indexOf(id);
        console.log(index)
        this.upiCheckedId.splice(index, 1);
        console.log("this.upiCheckedId", this.upiCheckedId)

        if (this.upiCheckedId.length > 0) {
        }
        else {
          this.upiCheckedId = [];
        }

    }
  }

}
