import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { UrlConstants } from 'src/app/_helpers/url-constants';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SaalryReleaseServiceService {
  private partyAddedSource = new Subject<void>();
  partyAdded$ = this.partyAddedSource.asObservable();
  notifyPartyAdded() {
    this.partyAddedSource.next();
  }



  constructor(public httpClient: HttpService) { }
  getAllSalaryData(data: {},userId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.getAllSalary +userId, 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  AddEarnigData(data: {},userId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.getAllSalary +userId, 'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

  getAllSalaryDataByEmployeeId(data: {},employeeId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.getSalaryByEmpoyeeId +employeeId, 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }

  PostEarningDeduct(data: {},userId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.AddEarningSalary +userId, 'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  } 
}
